// собираю футер

.page-footer {
  background-color: #191c26;
  @media screen and (min-width: 1200px) {
    display: flex;
  }
}

.footer-contacts {
  padding-top: 84px;
  padding-bottom: 84px;

  background-color: #191c26;
  background-image: url(../images/footer/footer-mob-x1.png);

  background-size: cover;

  @media screen and (min-width: $tablet) {
    max-width: 768px;
    background-image: url(../images/footer/footer-tab-x1.png);

    background-size: cover;

    display: flex;
  }

  @media screen and (min-width: $desktop) {
    max-width: 1200px;
    background-image: url(../images/footer/footer-desk-x1.png);

    background-size: cover;
  }

  @media screen and (min-width: $desktop) {
    display: flex;
    flex-direction: column;
    width: 625px;
  }
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .footer-contacts {
    background-image: url('../images/footer/footer-mob-x2.png');
  }
}

@media screen and (min-device-pixel-ratio: 3),
  screen and (min-resolution: 288dpi),
  screen and (min-resolution: 3dppx) {
  .footer-contacts {
    background-image: url('../images/footer/footer-mob-x3.png');
  }
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  @media screen and (min-width: $tablet) {
    .footer-contacts {
      background-image: url('../images/footer/footer-tab-x2.png');
    }
  }
}

@media screen and (min-device-pixel-ratio: 3),
  screen and (min-resolution: 288dpi),
  screen and (min-resolution: 3dppx) {
  @media screen and (min-width: $tablet) {
    .footer-contacts {
      background-image: url('../images/footer/footer-tab-x3.png');
    }
  }
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  @media screen and (min-width: $desktop) {
    .footer-contacts {
      background-image: url('../images/footer/footer-desk-x2.png');
    }
  }
}

@media screen and (min-device-pixel-ratio: 3),
  screen and (min-resolution: 288dpi),
  screen and (min-resolution: 3dppx) {
  @media screen and (min-width: $desktop) {
    .footer-contacts {
      background-image: url('../images/footer/footer-desk-x3.png');
    }
  }
}

.footer-header {
  padding-bottom: 40px;

  text-transform: capitalize;
  text-align: center;

  font-family: Raleway;

  font-size: 42px;
  line-height: 1.16;

  letter-spacing: 0.05em;

  color: #ffffff;
}

.address-info {
  margin-bottom: 44px;

  display: flex;
  flex-direction: column;
  // font-family: Open Sans;
  font-style: normal;
  // font-weight: normal;
  font-size: 18px;
  line-height: 1.67;
  letter-spacing: 0.02em;

  color: #9da4bd;
}

.footer-svg {
  display: block;
  margin-right: 16px;

  fill: currentColor;
  width: 24px;
  height: 24px;
}

// .map-pin{
//     display: block;
//     width: 24px;
//     height: 24px;
// }

.footer-address {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  margin-bottom: 20px;

  @media screen and (min-width: $tablet) {
    max-width: 250px;
  }
}

.footer-tel {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  margin-bottom: 20px;
  text-decoration: none;
}

.footer-mail {
  text-decoration: underline;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  margin-bottom: 20px;
}

.footer-mail,
.footer-tel {
  color: #9da4bd;
}

.working-container {
  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    margin-top: 16%;
    margin-left: 150px;
  }
}

.working-time {
  color: #ffffff;
  padding-bottom: 20px;
}

.working-time::before {
  content: '';
  display: inline-block;
  width: 60px;
  height: 1px;
  margin-right: 20px;
  margin-bottom: 3px;
  background: rgba(157, 164, 189, 0.6);
}

.working-hours {
  margin: 0px;
}

.footer-socials {
  display: inline-flex;
  justify-content: space-around;

  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.2em;

  color: $primary-text-color;

  @media screen and (min-width: 768px) {
    margin-left: auto;
  }
}

.footer-items {
  list-style: none;

  &:nth-child(-n + 1) {
    margin-right: 40px;
  }
}

.footer-third {
  display: flex;
  padding: 44px 15px;

  @media screen and (min-width: $tablet) {
    display: flex;
  }
}

.footer-line::before {
  content: '';
  display: inline-block;
  margin-right: 40px;
  margin-bottom: 2px;
  width: 60px;
  height: 1px;

  background: $primary-text-color;
}

.copyright {
  display: inline-flex;
  align-self: flex-end;

  padding-right: 21px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.2em;

  @media screen and (max-width: 767px) {
    padding-top: 35px;
  }

  @media screen and (min-width: $tablet) {
    display: inline-block;
    justify-self: center;
  }
}
